import React, { createRef } from 'react'
import PropTypes from 'prop-types'

import WizardAccordion from '_molecules/wizard-accordion'
import Input from '_atoms/input'
import SkillLevelField from '_atoms/skill-level-field'
import useInputFocus from '_hooks/use-input-focus'

const SkillWizard = ({ 
  hasFocus, 
  skill, 
  onChange,
  onFocus,
  ...wizardAccordionProps
}) => {
  const firstInputRef = createRef()

  useInputFocus(firstInputRef, hasFocus, onFocus)

  const handleChange = event => {
    onChange(event.target.name, event.target.value)
  }

  const handleSkillChange = value => {
    onChange('level', value)
  }

  return (
    <WizardAccordion
      title={skill.name}
      subtitle={skill.level.label}
      {...wizardAccordionProps}
    >
      <Input
        id="skillName"
        name="name"
        label="Skill"
        value={skill.name} 
        onChange={handleChange}
        ref={firstInputRef}
      />
      <SkillLevelField
        id="skillLevel"
        name="level"
        label="Level"
        value={skill.level}
        onChange={handleSkillChange}
      />
    </WizardAccordion>
  )
}

SkillWizard.propTypes = {
  hasFocus: PropTypes.bool,
  skill: PropTypes.shape({
    name: PropTypes.string,
    level: PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.number,
    }),
  }), 
  onChange: PropTypes.func,
  onFocus: PropTypes.func,
}

SkillWizard.defaultProps = {
  hasFocus: false,
  skill: {
    name: '',
    level: {
      label: '',
      value: 1,
    },
  }, 
  onChange: () => {},
  onFocus: () => {},
}

export default SkillWizard
